.button2{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 32px;
    background: #FFFFFF;
    border: 1px solid rgba(153, 153, 153, 1);
    border-radius: 4px;
    padding: 1px 11px;
    margin: 10px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.button2 > .circle{
    border-radius: 50%;
    height: 10px;
    width: 10px;
    background: #ddd;
    opacity: 0.5;
    display: inline-block;
    -webkit-animation: 1s big forwards;
            animation: 1s big forwards;
    position: absolute;
    z-index: 0;
}
.button2 > .value{
    z-index: 1;
}
@-webkit-keyframes big{
    0%{
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
    }
    100%{
        -webkit-transform: scale(10);
                transform: scale(10);
    }
}
@keyframes big{
    0%{
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
    }
    100%{
        -webkit-transform: scale(10);
                transform: scale(10);
    }
}


